<template>
  <div class="w100 Contenido">
    <Modal ref="Modal" v-show="ModalVisible" @Cerrar="CerrarModal" /> 
    <div class="TituloSeccionRegistros aliLeft">
      <b>Productor:</b>
      {{ NombreProductor() }}
      <br />
      <b>Establecimiento:</b>
      {{ NombreEstablecimiento() }}
    </div>
    <div class="DivInfoData" v-bind:class="{ 'Visible': !DataActualizada }" ref="DivInfoData">
      <div class="fLeft">
        Info recuperada de fecha: <span ref="FechaData"></span>
      </div>
      
      
      <div class="fRight" v-on:click.stop="RefrescarInfo(InfoPluviometro._IdPluviometro)">
        <font-awesome-icon  icon="sync-alt" size="4x" :style="{ color: 'white'}"  class="IconoActualizar fLeft"/>
        <!-- Actualizar -->
        <div class="clear"></div>
      </div>
      
      <div class="clear"></div>
    </div>
    <table>
      <thead>
        <tr>
          <th>Valor</th>
          <th>Fecha</th>
          <th class="pR">
            <font-awesome-icon 
            v-on:click.stop="NuevoRegistro()" 
            v-show="this.$parent.Datos.AdminLogueado()"
            icon="plus-circle" size="4x" :style="{ color: '#005625'}" class="IconoAgregar Centro"/>
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(p, index) in RegistrosPag" :key="index">
          <td class="c1">{{ p._Valor }} mm</td>
          <td>{{ p._FechaAlta }}</td>
          <td class="c3">
            <font-awesome-icon 
              v-show="p._Id > 0 && AdminLogueado() && Online()"
              v-on:click.stop="EliminarRegistro(p._Id, InfoPluviometro._IdPluviometro)"
              icon="trash-alt"
              size="4x"
              :style="{ color: '#934141' }"
              class="h100 IconoEliminar cP"
            />
            <font-awesome-icon 
              v-show="p._Id <= 0"
              icon="spinner"
              size="4x"
              :style="{ color: '#000000' }"
              class="h100 IconoEliminar cP fa fa-spinner fa-spin"
            />
          </td>
        </tr>
      </tbody>
    </table>

    <div class="SinRegistros" v-show="this.MostrarSinRegistros">
      Aún no hay registros...
    </div>

    <sliding-pagination
      v-show="totalPages > 1"
      :slidingEndingSize="2"
      :slidingWindowSize="2"
      :current="currentPage"
      :total="totalPages"
      @page-change="CambioDePagina"
    ></sliding-pagination>
    <br />
    <br />
    <apexcharts width="100%" type="bar" :options="chartOptions" :series="series"></apexcharts>
    <br />
    <br />
    <router-link class="BotonVerMapa dIB" to="/vermapapluviometros">Ver Mapa</router-link>
  </div>
</template>

<script>
import SlidingPagination from "vue-sliding-pagination";
import Modal from '../components/Utilidades/ModalNuevoRegistroPluviometro';
import axios from "axios";
import Clases from "../Auxiliares/Clases.js";
import ManejoDatos from "../Auxiliares/ManejoDatos.js";
import VueApexCharts from "vue-apexcharts";

export default {
  name: "Pluviometro",
  data() {
    return {
      InfoPluviometro: null, //El modelo lo setea la API
      RegistrosPag: [],
      MostrarSinRegistros: false,
      DataActualizada: true,
      _UserOk: false,
      Error: null,
      ModalVisible: false,
      //Paginado
      currentPage: 0,
      totalPages: 0,
      ItemsPorPagina: 5,
      RecalcularPaginado(_InfoPluviometro) {
        this.totalPages = Math.ceil(
          _InfoPluviometro._Registros.length / this.ItemsPorPagina
        );
        var _Aux = [];
        for (let i = 0; i < _InfoPluviometro._Registros.length; i++) {
          if (i >= (this.currentPage - 1) * this.ItemsPorPagina) {
            if (i < this.currentPage * this.ItemsPorPagina) {
              //this.RegistrosPag.push(_InfoPluviometro._Registros[i]);
              _Aux.push(_InfoPluviometro._Registros[i]);
            }
          }
        }
        this.RegistrosPag = _Aux;
        this.MostrarSinRegistros = this.RegistrosPag.length <= 0;
      },
      NombreProductor(){
        if (this.InfoPluviometro != null){
          return this.InfoPluviometro._Productor;
        }
        return "";
      },
      NombreEstablecimiento(){
        if (this.InfoPluviometro != null){
          return this.InfoPluviometro._Establecimiento;
        }
        return "";
      },
      Online(){
        return this.$parent.Datos.AppOnline();
      },
      AdminLogueado(){
        return this.$parent.Datos.AdminLogueado();
      },
      //Grafico
      chartOptions: {
        chart: {
          id: "basic-bar",
        },
        xaxis: {
          categories: [],
          labels: {
            show: true,
            rotate: -45,
            rotateAlways: true,
          },
        },
        yaxis: {
          title: {
            text: "mm",
          },
        },
        noData: {
          text: "Loading...",
        },
      },
      series: [
        {
          name: "ml.",
          data: [],
        },
      ],
    };
  },
  mounted() {
    this._UserOk = this.$parent.Datos.ChequearSesionUsuario(this.$router);
    if (this._UserOk) {
      this.RefrescarInfo(this.$route.params.Id);
    }
  },
  methods: {
    RefrescarInfo(_IdPluviometro){
      if(this.VistaActual(_IdPluviometro)){
        this.IntentoObtenerDataDeCache(_IdPluviometro);
      }
      ManejoDatos.ObtenerRegistrosPluviometro(_IdPluviometro, this);
    },
    VistaActual(_IdPluviometro){
      return (this.$route.params.Id == _IdPluviometro && this.$route.name == "Pluviometro");
    },
    AuxiliaresVista(_IdPluviometro){
      if(this.VistaActual(_IdPluviometro)){
        this.currentPage = 1;
        this.AgregarPendientes(_IdPluviometro);
        this.RecalcularPaginado(this.InfoPluviometro);
        this.ActualizarGrafico();
      }
    },
    AgregarPendientes(_IdPluviometro){
      var _Pendientes = Clases.RecuperarJsonDeStorage(this.KeyRegistrosPendientes(_IdPluviometro));
      if(_Pendientes != null){
        _Pendientes.forEach(_Reg => {
          var _Reg = {
            _Id: 0,
            _Valor: _Reg.Valor,
            _FechaAlta: new Clases.Fecha()._Fecha
          };
          this.InfoPluviometro._Registros.unshift(_Reg);
        });
      }
    },
    IntentoObtenerDataDeCache(_IdPluviometro){
      try {
        var _Data = Clases.RecuperarJsonDeStorage(ManejoDatos.KeyDataCacheadaPluviometros(_IdPluviometro));
        if (_Data != null){
          this.InfoPluviometro = _Data._Json;
          this.$refs.FechaData.innerHTML = _Data._Fecha._FechaYHora;
        }
      } catch (e) {
        console.log(e);
      }
    },
    KeyRegistrosPendientes(_IdPluviometro){
      return ManejoDatos._KeyRegistrosPendientesGeneralPluviometros + _IdPluviometro;
    },
    CambioDePagina(selectedPage) {
      this.currentPage = selectedPage;
      this.RecalcularPaginado(this.InfoPluviometro);
    },
    ActualizarGrafico() {
      var Valores = [];
      var Categorias = [];

      this.InfoPluviometro._Registros.forEach((obj) => {
        Valores.push(obj._Valor);
        Categorias.push(obj._FechaAlta);
      });

      Valores = Valores.reverse();
      Categorias = Categorias.reverse();

      this.chartOptions = {
        xaxis: {
          categories: Categorias.UltimosCincoItems(),
        },
      };

      this.series = [
        {
          data: Valores.UltimosCincoItems(),
        },
      ];
    },
    NuevoRegistro(){
      this.$refs.Modal.Setear(this.InfoPluviometro._IdPluviometro);
      this.VerModal();  
    },
    VerModal() {
      this.ModalVisible = true;
      setTimeout(() => {
          this.$refs.Modal.$refs.Valor.focus()
      }, 200);
    },
    CerrarModal() {
      this.ModalVisible = false;
    },
    EliminarRegistro(_Id, _IdPluviometro){
      var _This = this;
      //alert(_Nombre);
      this.$confirm({
        title: "ATENCIÓN",
        message: "Estás seguro de eliminar el registro?",
        button: {
          yes: 'Si',
          no: 'No'
        },
        callback: confirm => {
          if(confirm){
            axios
              .post(_This.$parent.Datos.UrlPosts() + "/AgroIdeas/BajaRegistroPluviometro", {
                IdUsuario: _This.$parent.Datos._User._ID,
                IdRegistro: _Id
              })
              .then(function (response) {
                _This.$parent.Notificacion("Registro eliminado!", "", "success", 3);
                _This.RefrescarInfo(_IdPluviometro);
              })
              .catch(function (error) {
                _This.$parent.Notificacion("Error!", "", "error", 3);
                console.error(error);
              })
              .then(function () {
                //console.log("Ok");//LLega siempre que no haya catch
              });
          }
        }
      })
    }
  },
  components: {
    SlidingPagination,
    apexcharts: VueApexCharts,
    Modal,
  },
};
</script>

<style lang="scss" scoped>
@import "../Css/Globales.css";
@import "../Css/Componentes.scss";

.svg-inline--fa.fa-w-18,
.svg-inline--fa.fa-w-14,
.svg-inline--fa.fa-w-16 {
  width: auto !important;
  margin: 0 5px;
}
.c1 {
  width: 40%;
}
.c3 {
  width: 20%;
}
.Verde {
  background-color: white;
  color: black;
  border: 2px solid #4caf50;
}

.Verde:hover {
  background-color: #4caf50;
  color: white;
}

.Azul {
  background-color: white;
  color: black;
  border: 2px solid #008cba;
}

.Azul:hover {
  background-color: #008cba;
  color: white;
}

.Gris {
  background-color: white;
  color: black;
  border: 2px solid #e7e7e7;
}

.Gris:hover {
  background-color: #e7e7e7;
}

.Negro {
  background-color: white;
  color: black;
  border: 2px solid #555555;
}

.Negro:hover {
  background-color: #555555;
  color: white;
}

.TituloSeccion {
  margin: 15px 0;
}

@media only screen and (max-width: 900px) {
  .table-wrapper {
    width: 90%;
    margin: 0 5%;
    overflow-x: scroll;
  }
}
</style>
