<template>
  <div class="w100 Contenido">
    <gmap-map
      ref="gmap"
      :center="center"
      :zoom="12"
      style="width:100%;  height: calc(100vh - 100px);"
      :map-type-id="mapTypeId"
      :options="{
        zoomControl: true,
        mapTypeControl: false,
        scaleControl: false,
        streetViewControl: false,
        rotateControl: false,
        fullscreenControl: true,
        disableDefaultUI: false
      }"
    >
      <gmap-marker
        :key="index"
        v-for="(m, index) in markers"
        :position="m.position"
        @click="toggleInfoWindow(m,index)"
      ></gmap-marker>

      <gmap-info-window
        :options="infoOptions"
        :position="infoWindowPos"
        :opened="infoWinOpen"
        @closeclick="infoWinOpen=false"
      >
        <div v-html="infoContent"></div>
        <br />
        <br />
        <apexcharts ref="graf" width="100%" type="bar" :options="chartOptions" :series="series" v-show="Registros.length > 0"></apexcharts>
      </gmap-info-window>
    </gmap-map>
  </div>
</template>

<script>
const MisClases = require("../Auxiliares/Clases.js");
import axios from "axios";
import VueApexCharts from "vue-apexcharts";

export default {
  name: "GoogleMap",
  components: {
    apexcharts: VueApexCharts,
  },
  data() {
    return {
      //a default center for the map
      map: null,
      mapTypeId: "satellite",
      infoContent: "",
      center: { lat: -30.77, lng: -62.77802 },
      infoWindowPos: {
        lat: 0,
        lng: 0,
      },
      infoWinOpen: false,
      currentMidx: null,
      //optional: offset infowindow so it visually sits nicely on top of our marker
      infoOptions: {
        pixelOffset: {
          width: 0,
          height: -35,
        },
      },
      markers: [],
      //Grafico
      Registros: [],
      chartOptions: {
        chart: {
          id: "basic-bar",
        },
        xaxis: {
          categories: [],
          labels: {
            show: true,
            rotate: -45,
            rotateAlways: true,
          },
        },
        yaxis: {
          title: {
            text: "mm",
          },
        },
        noData: {
          text: "No hay registros cargados"
        },
      },
      series: [
        {
          name: "ml.",
          data: [],
        },
      ],
    };
  },
  mounted() {
    this._UserOk = this.$parent.Datos.ChequearSesionUsuario(this.$router);
    if (this._UserOk) {
      //set bounds of the map
      this.ObtenerDatos();
      this.$refs.gmap.$mapPromise.then((map) => {
        const bounds = new google.maps.LatLngBounds();
        for (let m of this.markers) {
          bounds.extend(m.position);
        }
        map.fitBounds(bounds);
      });
    }
  },
  methods: {
    ObtenerDatos() {
      var _This = this;
      try {
        axios
          .post(
            _This.$parent.Datos.UrlPosts() + "/AgroIdeas/MapaPluviometros",
            {
              IdUsuario: _This.$parent.Datos._User._ID,
            }
          )
          .then(function (response) {
            _This.InfoMapa = response.data;

            let Marcadores = [];

            _This.InfoMapa.forEach((obj) => {
              _This.Registros[obj._Id] = obj._Registros;
              let _Geo = new MisClases.GeoLocation();
              _Geo.lat = parseFloat(obj._Geo._Lat);
              _Geo.lng = parseFloat(obj._Geo._Lng);

              let _Marcador = new MisClases.Marcador();
              _Marcador.id= obj._Id
              _Marcador.name = obj._NombreProductor;
              _Marcador.localidad = obj._Localidad;
              _Marcador.description = obj._Establecimiento;
              _Marcador.position = _Geo;
              Marcadores.push(_Marcador);
            });

            _This.markers = Marcadores;

            _This.CargarMarcadores();
            ActualizarGrafico(_This.Registros[1]);
          })
          .catch(function (error) {
            _This.Error = error;
          })
          .then(function () {});
      } catch (error) {
        console.log(error);
      }
    },
    CargarMarcadores() {
      this.$refs.gmap.$mapPromise.then((map) => {
        const bounds = new google.maps.LatLngBounds();
        for (let m of this.markers) {
          bounds.extend(m.position);
        }
        map.fitBounds(bounds);
      });
    },
    ActualizarGrafico(registros) {
      var Valores = [];
      var Categorias = [];

      registros.forEach((obj) => {
        var contador = 1;
        Object.entries(obj).forEach(([key, value]) => {
          if (contador % 2) {
            Valores.push(value);
          } else {
            Categorias.push(value);
          }
          contador++;
        });
      });

      this.chartOptions = {
        xaxis: {
          categories: Categorias.UltimosCincoItems(),
        },
      };

      this.series = [
        {
          data: Valores.UltimosCincoItems(),
        },
      ];

    },
    toggleInfoWindow: function (marker, idx) {
      this.infoWindowPos = marker.position;
      this.infoContent = this.getInfoWindowContent(marker);

      //check if its the same marker that was selected if yes toggle
      if (this.currentMidx == idx) {
        this.infoWinOpen = !this.infoWinOpen;
      }
      //if different marker set infowindow to open and reset current marker index
      else {
        this.infoWinOpen = true;
        this.currentMidx = idx;
        console.log(marker.id);
        if (this.Registros[marker.id].length > 0) {
          this.ActualizarGrafico(this.Registros[marker.id]);
        }
        else{
          this.ActualizarGrafico([]);
        }
      }
    },

    getInfoWindowContent: function (marker) {
      return `<div class="card">
                <div class="card-content">
                  <div class="media">
                    <div class="media-content">
                      <p class="title is-4">${marker.id} - ${marker.name}, ${marker.localidad}</p>
                    </div>
                  </div>
                  <div class="content">
                    ${marker.description}
                  </div>
                </div>
              </div>`;
    },
  },
};
</script>

<style lang="scss" scoped>
@import "../Css/Componentes.scss";
</style>
